function contactForm() {
  const form = document.querySelector('.contacts__form');
  const btn = document.querySelector('.contacts__btn');
  const title = document.querySelector('.contacts__text-2');
  const contactsTop = document.querySelector('.contacts__top');
  const formInputs = document.querySelectorAll('form input');

  formInputs.forEach((inputElem) => { //    input
    const errorElem = inputElem.nextElementSibling; //    input   span  
    inputElem.addEventListener('focus', () => {
      errorElem.textContent = ''; //   
      errorElem.className = 'error'; //    
      inputElem.style.borderColor = 'unset'; //   
    });
    inputElem.addEventListener('blur', () => {
      validCheck(inputElem, errorElem);
    });
  });

  bindPostData(form);

  function validCheck(inputElem, errorElem) {
    if (!inputElem.validity.valid) {
      showError(inputElem, errorElem); //    
      inputElem.style.borderColor = 'red';
      return false;
    }
    return true;
  }

  function showError(inputElem, errorElem) { //    
    if (inputElem.validity.valueMissing) { //      
      errorElem.textContent = ' ';
    } else if (inputElem.validity.tooShort) {
      errorElem.textContent = `  `;
    } else if (inputElem.validity.patternMismatch && inputElem.matches('[type="email"]')) {
      //  phone       type="phone"   
      errorElem.textContent = '  email';
    }
    errorElem.className = 'error_active';
  }

  function bindPostData(form) {
    form.addEventListener('submit', (event) => {
      event.preventDefault(); //   
      const eventInputs = event.target.querySelectorAll('input'); //  input  ,   
      eventInputs.forEach((input) => validCheck(input, input.nextElementSibling)); //   input        
      for (const input of eventInputs) {
        if (!validCheck(input, input.nextElementSibling)) { //    ,       
          return;
        }
      }

      btn.setAttribute('disabled', ''); //  ,    

      const formData = new FormData(form); //   ,   input    name=""

      postData('../form-processing.php', formData) //      
        .then((result) => { //   promise
          showThanks(result);
        })
        .catch(() => { //  reject ()
          form.style.display = 'none';
          title.textContent = `    `;
        })
        .finally(() => { //    
          form.reset(); //  
          btn.removeAttribute('disabled', '');
        });
    });
  }

  async function postData(url, data) { //      
    const result = await fetch(url, { // await    fetch
      method: 'POST', // POST  , GET 
      body: data, //  ,   GET,   
    });
    return await result.json(); //      PROMISE
  }

  function showThanks(result) { //    
    if (result.code == 1) { //      php 
      title.textContent = `!       `;
      form.style.display = 'none';
    } else {
      title.textContent = `  `;
      form.style.display = 'none';
      const errMessageBlock = document.createElement('div');
      contactsTop.append(errMessageBlock);
      errMessageBlock.classList.add('contacts__error');
      errMessageBlock.innerHTML = `
      <p>${result.nameErr}</p>
      <p>${result.emailErr}</p>
      `;
    }
  }
}

export default contactForm;