function animation() {

  const targets = [ // ,    
    document.querySelector('.about-me__skills'), //      aboutme
    document.querySelector('.progress'), //     
  ];

  const toolBlocks = document.querySelectorAll('.tool'); //    tools
  toolBlocks.forEach((item, i) => {
    targets.push(item); //     nodelist   targets
    item.style.opacity = '0'; //  
    item.style.transition = `all 0.6s linear ${i / 5}s`; //      
  });

  const observer = new IntersectionObserver(callback, {
    rootMargin: '0px 0px -100px 0px', //    CSS,    
  }); //   observer. {}   

  targets.forEach((target) => {
    observer.observe(target); //  observe  ""     target
  });

  const skillBlocks = document.querySelectorAll('.skill'); //    about-me,    
  skillBlocks.forEach((item, i) => {
    item.style.transform = `translateX(100vw)`; //     
    item.style.transition = `all 0.6s linear ${i / 5}s`; //      
  });

  function callback(entries) { // //  -    
    entries.forEach((entry) => { // entry   

      if (entry.isIntersecting && entry.target.matches('.about-me__skills')) { // ,       (     )
        skillBlocks.forEach((item) => { // c  skills    
          item.style.transform = `translateX(0)`;
        });
        observer.unobserve(entry.target); //   
      }

      if (entry.isIntersecting && entry.target.matches('.progress')) { // ,       (     )
        percentsAnimation(); //  
        observer.unobserve(entry.target); //   
      }

      if (entry.isIntersecting && entry.target.matches('.tool')) { // ,       (     )
        entry.target.style.opacity = '1'; //   
        observer.unobserve(entry.target); //   
      }

    });
  }

  function percentsAnimation() { //  

    const progressFillBlocks = document.querySelectorAll('.progress__bar_fill'); //   
    const progressCounterBlocks = document.querySelectorAll('.progress__counter'); //   

    const value = [100, 90, 25]; // ,    

    value.forEach((item, i) => {
      let p = 0;
      const id = setInterval(frame, 10); //     
      function frame() {
        if (p === item) {
          clearInterval(id);
        } else {
          p++;
          progressFillBlocks[i].style.width = `${p}%`;
          progressCounterBlocks[i].textContent = `${p}%`;
        }
      }
    });
  }
}

export default animation;
